const ARTICLE_BASE_URL = "/api/article";

const invoke = async args => {
    const opts = args.opts || {
        method: "GET",
    };
    const res = await fetch( args.url, opts );
    return res.json();
};

export const getArticleInfo = async name => {
    return invoke( {
        url: `${ARTICLE_BASE_URL}/${name}`,
    } );
};

export const upvoteArticle = async name => {
    return invoke( {
        url: `${ARTICLE_BASE_URL}/${name}/upvote`,
        opts: {
            method: "POST",
        },
    } );
};

export const addArticleComment = async ( name, comment ) => {
    return invoke( {
        url: `${ARTICLE_BASE_URL}/${name}/add-comment`,
        opts: {
            method: "POST",
            headers: {
                "Content-Type": "application/json",
            },
            body: JSON.stringify( comment ),
        },
    } );
};
