import React, { useEffect, useState } from "react";
import { AddCommentForm, ArticlesList, CommentsList, UpvotesSection } from "../components";
import { getArticleInfo } from "../services";
import articles from "./article-content";
import { NotFoundPage } from "./NotFoundPage";

export const ArticlePage = ( props ) => {
    const { name } = props.match.params;
    const article = articles.find( a => a.name === name );
    const [ articleInfo, setArticleInfo ] = useState( {
        upvotes: 0,
        comments: [],
    } );
    useEffect( () => {
        (async () => setArticleInfo( await getArticleInfo( name ) ))();
    }, [ name ] );

    if ( !article ) return <NotFoundPage/>;
    return (
        <>
            <h1>{article.name}</h1>
            <UpvotesSection articleInfo={articleInfo} setArticleInfo={setArticleInfo}/>
            {article.content.map( ( c, index ) => (
                <p key={index}>{c}</p>
            ) )}
            <CommentsList comments={articleInfo.comments}/>
            <AddCommentForm articleInfo={articleInfo} setArticleInfo={setArticleInfo}/>
            <h3>Related Articles...</h3>
            <ArticlesList articles={
                articles.filter( a => a.name !== name )
            }/>
        </>
    );
};
