import React, { useState } from "react";
import { addArticleComment } from "../services";

export const AddCommentForm = ( { articleInfo, setArticleInfo } ) => {
    const [ username, setUsername ] = useState( "" );
    const [ commentText, setCommentText ] = useState( "" );
    const handleUsernameChange = e => setUsername( e.target.value );
    const handleCommentTextChange = e => setCommentText( e.target.value );
    const addComment = async () => {
        setArticleInfo( await addArticleComment( articleInfo.name, {
            username,
            text: commentText,
        } ) );
        setUsername( "" );
        setCommentText( "" );
    };
    return (
        <div id="add-comment-form">
            <h3>Add a Comment</h3>
            <label>
                Username:
                <input type="text" value={username} onChange={handleUsernameChange}/>
            </label>
            <label>
                Comment:
                <textarea rows={4} cols={5} value={commentText} onChange={handleCommentTextChange}/>
            </label>
            <button onClick={addComment}>Submit</button>
        </div>
    );
};
