import React from "react";
import { upvoteArticle } from "../services";

export const UpvotesSection = ( { articleInfo, setArticleInfo } ) => {

    const upvote = async () => {
        setArticleInfo( await upvoteArticle( articleInfo.name ) );
    };

    return (
        <div id="upvotes-section">
            <button onClick={upvote}>Upvote</button>
            <p>This article has been upvoted {articleInfo.upvotes} times</p>
        </div>
    );
};
